export default {
	name: "basics",
	props: ["UserInfoB", "staffList", "merchantInfo", "swiperList","recStaff"],
	data() {
		return {
			status: 2,
			orderMethod: 0,
			filterMethod: 0,
			modalName: null,
			cardCur: 0,
			telNum: null,
			// swiperList: [{
			// 	id: 0,
			// 	type: 'image',
			// 	url: '../../static/girls/1.jpg'
			// }, {
			// 	id: 1,
			// 	type: 'image',
			// 	url: '../../static/girls/2.jpg',
			// }, {
			// 	id: 2,
			// 	type: 'image',
			// 	url: '../../static/girls/3.jpg'
			// }],
			dotStyle: false,
		};
	},
	created(){
		console.log(this.$props.staffList);
	},
	methods:{
		Call(index){
			if(this.$props.UserInfoB == undefined){
				uni.showModal({
					title: '请授权微信登录',
					success(res) {
						if(res.confirm){
							uni.reLaunch({
								url: '../login/login'
							})
						}
					}
				})
			} else {
				uni.makePhoneCall({
					phoneNumber: this.telNum[index]
				})
			}
			
		},
		CallMerchant(){
			this.telNum = JSON.parse(this.merchantInfo.tel) 
			this.modalName = "bottomModal"
			
		},
		showImg(img){
			uni.previewImage({
				urls: [img]
			})
		},
		changeStatus(e){
			this.status = e.currentTarget.dataset.status
		},
		Collect(iindex){
			var that = this
			if(this.$props.UserInfoB == undefined){
				uni.showModal({
					title: '请授权微信登录',
					success(res) {
						if(res.confirm){
							uni.reLaunch({
								url: '../login/login'
							})
						}
					}
				})
			} else {
				this.$emit('Collect', iindex)
				this.staffList[iindex].isCollection = !this.staffList[iindex].isCollection
				// 因为先改样子再变，所以要反过来
				if(this.$props.staffList[iindex].isCollection == false){
					uni.request({
						method: "POST",
						header: {
							token: that.Token
						},
						url: that.Server + 'userStaff/del',
						data: {
							staffId: this.$props.staffList[iindex].id
						},
						success(res) {
							if(res.data.code == 0){
								
							} else if (res.data.code == 101) {
								uni.showToast({
									icon: 'none',
									title: '登录超时，将重新授权登录',
									duration: 500
								})
								setTimeout(() => {
									uni.reLaunch({
										url: '../login/login'
									})
								}, 500)
							} else {
								that.$emit('Collect', iindex)
								uni.showToast({
									icon: 'none',
									title: res.data.msg
								})
							}
						}
					})
				} else {
					uni.request({
						method: "POST",
						header: {
							token: that.Token
						},
						url: that.Server + 'userStaff/add',
						data: {
							staffId: this.$props.staffList[iindex].id
						},
						success(res) {
							if(res.data.code == 0){
								
							} else if (res.data.code == 101) {
								uni.showToast({
									icon: 'none',
									title: '登录超时，将重新授权登录',
									duration: 500
								})
								setTimeout(() => {
									uni.reLaunch({
										url: '../login/login'
									})
								}, 500)
							} else {
								uni.showToast({
									icon: 'none',
									title: res.data.msg
								})
							}
						}
					})
				}
			}
			
		},
		GetStaffList(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'staff/list',
				data: {
					merchantId: that.MerchantId
				},
				success(res) {
					if(res.data.code == 0){
						for(var i in res.data.data){
							that.staffList.push(res.data.data[i])
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: 'none',
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
				}
			})
		},
		goSearch(){
			if(this.$props.UserInfoB == undefined){
				uni.showModal({
					title: '请授权微信登录',
					success(res) {
						if(res.confirm){
							uni.reLaunch({
								url: '../login/login'
							})
						}
					}
				})
			} else {
				uni.navigateTo({
					url: '../search/search'
				})
			}
			
		},
		book(e){
			this.modalName = null
			uni.navigateTo({
				url: "../people-detail/people-detail?id=" + e.currentTarget.dataset.key
			})
		},
		bookB(id){
			this.modalName = null
			uni.navigateTo({
				url: "../people-detail/people-detail?id=" + id
			})
		},
		viewDetail(){
			this.modalName = "book"
		},
		hideModal(){
			this.modalName = null
		},
		changeModal(e){
			this.modalName = e.currentTarget.dataset.name
		},
		showLocation(){
			console.log(123);
			uni.openLocation({
				latitude: parseFloat(this.merchantInfo.lat) ,
				longitude: parseFloat(this.merchantInfo.lon),
				address: this.merchantInfo.address,
				name: this.merchantInfo.title,
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				}
			})
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
	},
	onShow() {
		console.log("success")
	}
}
